@media (max-width: 992px){
.react-datepicker {
    color: #000;
    font-size: 14px;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-weight: 300;
    margin: 0;
  
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.react-datepicker,
.react-datepicker__month-container{
    width: 100%;
}
.react-datepicker__day-name, .react-datepicker__day, .react-datepicker__time-name {
    width: 14%;
    text-align: center;
    margin: 0;
    padding: 0;
    padding:0.4rem;
    margin: 1px;
    font-size:25px;
    position: relative;
    
}

 
 
.react-datepicker__month--selecting-range .react-datepicker__day--in-range, .react-datepicker__month--selecting-range .react-datepicker__month-text--in-range, .react-datepicker__month--selecting-range .react-datepicker__quarter-text--in-range {
    background-color: #216ba5;
    color: #FFF;
}
 
.react-datepicker__current-month, .react-datepicker-time__header, .react-datepicker-year-header{
    font-size: 25px;
}

.date-range{
    margin-top: 10px;
    font-size: 25px;
    color: #606060;
    text-align: center;
}
}