.form-cad{
  padding:20px;
}

.form-cad form{
    padding: 30px 12px;
    background: #ffffff;
    box-shadow: 0 2px 5px 0 rgba(0,0,0,.26);
}
.form-cad form > div{
    margin-bottom: 16px;
}

.box-resumo{
    padding: 12px;
    background: #ffffff;
    box-shadow: 0 2px 5px 0 rgba(0,0,0,.26);
    line-height: 1.846;
    font-size: 1rem;
    color: #555;
}

.box-resumo h2{
    color: #242424;
    font-size: 20px;
    font-weight: 300;
    margin-bottom: 10px;
}
 
.box-resumo h3{
    margin-top: 10px;
    color: #0071c2;
}

.inp-group{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 5px 8px;
    
}

 
.box-value{
    text-align: center;
    padding:20px 0;
}

.box-value h1{
 padding-top: 5px;    
color: #0071c2;
}